// ADVANCED-AUTOCOMPLETE.ts
//---------------------------

import { Component, Input } from '@angular/core';

/**
 * Interface for AdvancedAutocompleteComponent's "data"
 */
export interface IAdvancedAutocompleteData {
  results?: IAdvancedAutocompleteResponse[];
  actions?: {
    advanced?: {
      text: string;
      payload: any;
      classes?: any;
    }
    showMore?: {
      text: string;
      payload: any;
      classes?: any;
    }
  }
  fallback: string;
  classes?: any;
  _meta?: any;
}

interface IAdvancedAutocompleteItem {
  title?: string;
  metadata?: Array<{ key?: string, value?: string }>;
  payload?: any;
}

interface IAdvancedAutocompleteResponse {
  items: IAdvancedAutocompleteItem[];
  group?: {
    title: string;
    classes?: string;
    icon?: string;
    payload?: any;
  }
}

@Component({
  selector: 'n7-advanced-autocomplete',
  templateUrl: './advanced-autocomplete.html'
})
export class AdvancedAutocompleteComponent {
  @Input() data: IAdvancedAutocompleteData;
  @Input() emit: any;

  onClick(payload){
    if(!this.emit) return;

    this.emit('click', payload);
  }
}